export const ChatBody: React.FC<{
  params: ChatParams;
  config: CoCoChatWindowParams;
  onBubbleHover?: (entry: ChatEntry) => void;
  onBubbleUnhover?: (entry: ChatEntry) => void;
  onBubbleSelectToggle?: (entry: ChatEntry) => void;
}> = ({
  config,
  params: { chat },
  onBubbleHover,
  onBubbleUnhover,
  onBubbleSelectToggle,
}) => {
  const classes = useStyles(config);

  return (
    <div className={classes.container}>
      {chat
        .filter((c) => c.text || c.isLoading)
        .map((entry) => (
          <ChatBubble
            key={entry.id}
            {...{
              config,
              params: { chat },
              entry,
              onHover: onBubbleHover,
              onUnhover: onBubbleUnhover,
              onSelectToggle: onBubbleSelectToggle,
            }}
          />
        ))}
    </div>
  );
};
